<template>
  <div :transition="slide">
    <v-header class="replace">
      <div slot="left" class="h-left">
        <a @click="goBack"><span class="close img"></span></a></div>
    </v-header>

    <v-content :type="'login'">
      <div class="login-menu">
        <p class="menu active" @click="loginWay($event,1)">快捷登录</p>
        <p class="menu" @click="loginWay($event,0)">账户登录</p>
      </div>
      <div class="error-msg">
      </div>
      <form class="login-form" onsubmit="return false;">
        <div class="login-box opa-8" v-show="loginType == 0">
          <div class="edit">
            <span class="img user"></span>
            <input type="tel" v-model="username" placeholder="手机号" maxlength="11"/>
          </div>
          <div class="edit">
            <span class="img password"></span>
            <span class="img eye close" @click="showPwd($event)"></span>
            <input type="password" v-model="password" placeholder="登录密码" id="password"/>
          </div>
        </div>

        <div class="login-box opa-8" v-show="loginType == 1">
          <div class="edit">
            <span class="img phone"></span>
            <span class="img delete" v-show="phone != ''" @click="deletePhone"></span>
            <input type="tel" v-model="phone" placeholder="手机号" maxlength="11"
                   onkeyup="value=value.replace(/[^\d]/g,'')"/>
          </div>
          <div class="edit">
            <span class="img code"></span>
            <span class="get-code">
              <span class="color-main text" v-show="seconds == 0" @click="getCode">获取验证码</span>
              <span class="color-9 text" v-show="seconds > 0">{{seconds}}s后重发</span>
            </span>
            <input type="text" v-model="code" placeholder="请输入验证码" maxlength="6"/>
          </div>
        </div>

        <input type="submit" :value="loginType == 1 ? '快捷登录' : '登录'" class="bg-color-main" @click="loginForm"/>
        <!-- <span class="sign-up link" v-link="{name:'register'}">免费注册</span> -->
        <span class="forget-pwd link opa-8" v-if="loginType == 0">忘记密码?</span>
        <span class="login-remark remark opa-8" v-else>未注册平台账号的手机号，登录时将自动注册，且代表您已同意《用户注册协议》</span>
      </form>
    </v-content>
  </div>
</template>
<script>
  import VHeader from '../components/Header'
  import VContent from '../components/Content'
  import $ from 'jquery'
  import {toast} from '../assets/js/toast'
  import {url, lt} from '../const'

  export default{
    route: {
      data () {
        if (lt.has(lt.keys.user)) {
          console.log('用户已登录');
          window.location.replace("#/user");
          return
        }
        if (this.isReady) {
          return {
            state: !this.state
          }
        }
      }
    },
    ready(){
      this.$on('ready', this.init);
      this.$emit('ready');
      this.isReady = true;
    },
    data(){
      return {
        isReady: false,
        state: false,

        loginType: 1,//0:用户名 1:验证码
        username: '',
        password: '',
        phone: '',
        code: '',
        seconds: 0
      }
    },
    methods: {
      init(){
        $("html").addClass("bg-img");
        //记录用户名
        if (lt.has(lt.keys.username)) {
          let _username = lt.getJson(lt.keys.username).username;
          this.phone = _username;
          this.username = _username;
        }
        ;
      },
      goBack(){
        if (this.$route.query.from) {
          window.history.back();
        } else {
          window.location.href = "#/home";
        }
      },
      getCode(){
        let vm = this;
        let reg = /^1[3-9]\d{9}$/;
        if (!vm.phone || !reg.test(vm.phone)) {
          toast.alert('请先输入正确的手机号');
          return;
        }
        if (vm.seconds == 0) {
          vm.seconds = 60;
        }
        let sec = setInterval(function () {
          vm.seconds = vm.seconds - 1;
        }, 1000);
        let s = vm.$watch('seconds', function () {
          if (vm.seconds == 0) {
            clearInterval(sec);
            s();
          }
        });
      },
      loginWay (event, type) {
        if (this.loginType == type) {
          return;
        }
        $(event.currentTarget).addClass("active").siblings().removeClass("active");
        if (type == 0) {
          this.code = '';
        } else {
          this.password = '';
        }
        this.loginType = type;
        $(".error-msg").html("");
      },
      loginForm(){
        //登录表单验证
        if (!this.loginFormCheck()) {
          return false;
        }
        if (this.loginType == 0) {
          this.$http.get(url.login,
                  {
                    username: this.username,
                    password: this.password,
                    phone: this.phone,
                    code: this.code,
                    loginType: this.loginType,
                    authencationType: 'AJAX'
                  }).then(//$(".error-msg").html("您的账号还未设置密码，请切换到快速登录，登录后请设置密码");
                  function ({data}) {
                    if (data.authenticated) {
                      lt.set(lt.keys.user, {
                        userId: data.userId,
                        userName: data.username
                      }, '必须关闭无痕浏览才可进行登录后操作');
                      console.log(data.userId + " userName:" + data.username);
                      //保存用户名用于记住登录手机号
                      lt.set(lt.keys.username, {username: data.username});

                      if (this.$route.query.from) {
                        window.location.replace("#" + this.$route.query.from);
                      } else {
                        window.location.replace("#/user");
                      }
                    } else {
                      if ('未设置密码' == data.resultMsg) {
                        $(".error-msg").html("您的账号还未设置密码，请切换到快速登录");
                      } else {
                        toast.alert(data.resultMsg);
                        $(".error-msg").html('');
                      }
                    }
                  }, function () {

                  }
          );
        } else {
          this.$http.get(url.register,
                  {
                    phone: this.phone,
                    code: this.code,
                    loginType: this.loginType,
                    authencationType: 'AJAX'
                  }).then(
                  function ({data:{data, code, success, resultMsg}}) {
                    if (success) {

                      lt.set(lt.keys.user, {
                        userId: data.userId,
                        userName: data.username
                      }, '必须关闭无痕浏览才可进行登录后操作');
                      console.log(data.userId + " userName:" + data.username);

                      //保存用户名用于记住登录手机号
                      lt.set(lt.keys.username, {username: data.username});

                      if (data.isRegister) {
                        window.location.replace("#/user/info?from=/user");
                      } else if (this.$route.query.from) {
                        console.log('from ='+ this.$route.query.from);
                          window.location.replace("#" + this.$route.query.from);
                      } else {
                        window.location.replace("#/user");
                      }
                    } else {
                      toast.alert(resultMsg);
                    }
                  }, function () {

                  }
          )
        }

        return false;
      },
      deletePhone(){
        this.phone = '';
      },
      showPwd(event){
        let obj = $(event.currentTarget);
        if (obj.is(".close")) {
          obj.removeClass("close").addClass("open");
          $("#password").attr("type", "text");
        } else {
          obj.removeClass("open").addClass("close");
          $("#password").attr("type", "password");
        }
      },
      loginFormCheck(){
        let errorMsg;
        if (this.loginType == 0) {
          if (!this.password) {
            errorMsg = "密码不能为空";
          }
          if (!this.username) {
            errorMsg = "用户名不能为空";
          }
        } else {
          if (!this.code) {
            errorMsg = "验证码不能为空";
          }
          if (!this.phone) {
            errorMsg = "手机号不能为空";
          }
        }
        if (errorMsg) {
          toast.alert(errorMsg);
          return false;
        } else {
          return true;
        }
      }
    },
    watch: {
      'state': function () {
        this.loginType = 1;//0:用户名 1:验证码
        this.username = '';
        this.password = '';
        this.phone = '';
        this.code = '';
        this.seconds = 0;
        this.$emit('ready');
      }
    },
    components: {VHeader, VContent}
  }
</script>
<style>
  .bg-img {
    background: url(../assets/img/bg/login-bg.jpg);
    background-size: 100%;
  }

  .my-header .close {
    background-image: url(../assets/img/icon/close.png);
  }

  .login-menu {
    margin-top: .15rem;
    height: .35rem;
    line-height: .35rem;
    border: .01rem solid #00cda9;
    border-radius: .05rem;
  }

  .login-menu .menu {
    float: left;
    width: 50%;
    text-align: center;
    height: 100%;
    color: #fff;
    font-size: .16rem;
  }

  .menu.active {
    background-color: #00cda9;
  }

  .error-msg {
    min-height: .25rem;
    padding: .05rem;
    font-size: .12rem;
    color: #fa3728;
  }

  .login-form {
    margin-top: .35rem;
    position: relative;
  }

  .login-box {
    background-color: #fff;
    border-radius: .05rem;
  }

  .login-box .edit {
    margin-left: .1rem;
    padding-right: .1rem;
    position: relative;
    height: .45rem;
  }

  .login-box .edit:first-child {
    height: .46rem;
    border-bottom: 1px solid #e5e5e5;
  }

  .edit .get-code {
    position: absolute;
    right: .1rem;
    top: .12rem;
  }

  .get-code .text {
    border-left: .01rem solid #dadada;
    padding-left: .1rem;
    height: .2rem;
    display: block;
  }

  .login-form input[type='submit'] {
    border: none;
    width: 100%;
    height: .4rem;
    color: #fff;
    border-radius: .05rem;
    margin-top: .25rem;
    margin-bottom: .25rem;
    font-size: .16rem;
  }

  .login-form .link {
    color: #fff;
    position: absolute;
    bottom: 0;
  }

  .login-form .sign-up {
    left: .1rem;
  }

  .login-form .forget-pwd {
    right: .1rem;
  }

  .img.user {
    background-image: url(../assets/img/icon/user.png);
  }

  .img.password {
    background-image: url(../assets/img/icon/lock.png);
  }

  .img.phone {
    background-image: url(../assets/img/icon/phone02.png);
  }

  .img.code {
    background-image: url(../assets/img/icon/security.png);
  }

  .eye.close {
    background-image: url(../assets/img/icon/eye-close.png);
  }

  .eye.open {
    background-image: url(../assets/img/icon/eye-open.png);
  }
</style>

<style scoped>
  .edit input {
    border: none;
    width: 100%;
    height: 100%;
    text-indent: .25rem;
    font-size: .14rem;
  }

  .edit .img {
    position: absolute;
    width: .22rem;
    height: .22rem;
    background-size: .22rem;
    top: .12rem;
  }

  .img.delete {
    background-image: url(../assets/img/icon/close02.png);
    right: .1rem;
    top: .15rem;
    width: .15rem;
    height: .15rem;
    background-size: .15rem;
  }

  .img.eye {
    right: .1rem;
    top: .18rem;
    width: .16rem;
    height: .11rem;
    background-size: .16rem;
  }

  .login-form .remark {
    display: block;
    color: #fff;
    text-align: center;
  }

  .login-form .remark a {
    color: #fff;
  }
</style>



